a
{
    text-decoration: none;
}

.bigContainerCardList 
{   
    display: flex;
    justify-content: center;
}

.bigContainerCardList .cardListWrapper
{
    display: flex;
    width: 80vw;
    justify-content: space-around;
    flex-wrap: wrap;
    padding-top: 3rem;
}

.bigContainerCardList .cardListWrapper .cardContainer 
{
    /* border: 1px solid pink */
    /* padding-right: 3rem;
    padding-bottom: 3rem */
    /* margin-right: 3rem;  */
    margin-bottom: 3rem;

}

.bigContainerCardList .cardListWrapper .cardImg
{
    height: 425px;
    width: 425px;
    background-size: cover !important;
    background-repeat: no-repeat !important;
    background-position: center center !important;
}

.bigContainerCardList .cardListWrapper .cardImg .captionImg
{
    position: relative;
    width: 100%;
    bottom: 1.5rem;
    left: 0px;
    font-family: 'Whitney-Book';
    font-weight: lighter;
    font-size: 0.8rem; 
    text-transform: uppercase;
    color: #333333;
}
