.burger {
    height: 3em;
    width: 3em;
    position: relative;
    font-size: 12px;
    cursor: pointer;
 }
    .burger .burgerlines:after {
      left: 0;
      top: -1em; }
    .burger .burgerlines:before {
      left: 1em;
      top: 1em; }
    .burger:after {
      content: '';
      display: block;
      position: absolute;
      height: 150%;
      width: 150%;
      top: -25%;
      left: -25%; }
    .burger .burgerlines {
      top: 50%;
      margin-top: -0.125em; }
      .burger .burgerlines, .burger .burgerlines:after, .burger .burgerlines:before {
        display: block;
        content: '';
        width: 100%;
        border-radius: 0.25em;
        background-color: black;
        height: 0.25em;
        position: absolute;
}
